.form-aligned select,
.form-stacked label,
.form-aligned label,
.form-stacked textarea,
.form-aligned textarea {
  display: block;
  margin: 0 0 15px;
}

@media (min-width: 992px) {
  .form-aligned input,
  .form-aligned textarea,
  .form-aligned select {
    display: inline-block;
    *display: inline;
    vertical-align: middle;
    *zoom: 1;
  }
  .form-aligned textarea {
    vertical-align: top;
  }
  .form-aligned label {
    display: inline-block;
    margin: 0 20px 0 0;
    text-align: right;
    vertical-align: middle;
    width: 200px;
  }
  .form-aligned .form-row-checkbox,
  .form-aligned .form-row-radio {
    margin: 0 0 15px;
    margin-left: 220px;
  }
  .form-aligned .form-controls {
    margin-left: 220px;
  }
}
.gform_wrapper li {
  list-style: none;
}
.gform_wrapper li::before {
  content: "";
  font-size: 0;
}
.gform_wrapper table {
  table-layout: initial;
}
.gform_wrapper [type=radio] {
  margin-left: 1px;
}